export default {
  props: {
    list: {
      type: Array,
      default: () => []
    },
    posObj: Object,
    styleObj: Object
  },
  data() {
    return {
      showDrop: false
    }
  },
  created() {

  },
  methods: {
    changeDrop(item) {
      this.$emit('toDrop',item)
    },
    blurHandle() {
      this.showDrop= false
      this.$emit('hideDrop',false)
    }
    
  }  
}
